@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600;800&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-116xl: 135px;
  --font-size-sm: 14px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-gray: #fefefe;

  /* Paddings */
  --padding-10xs: 3px;
  --padding-4xl: 23px;
  --padding-13xl: 32px;

  /* Border radiuses */
  --br-26xl: 45px;
  --br-980xl: 999px;
}
